import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import 'css-doodle'
Vue.config.productionTip = false

// ÁùÄÈáçÊñáÊú¨
const options = {
  // Turn on/off animation when annotating.
  animate: true,
  // Duration of the animation in milliseconds.
  animationDuration: 800,
  // Representing the color of the annotation sketch.
  color: 'currentColor',
  // Width of the annotation strokes.
  strokeWidth: 1,
  // (in pixels) Padding between the element and roughly where the annotation is drawn.
  padding: 5,
  // To annotate multiline text (each line separately), set this property to true.
  multiline: false,
  // By default annotations are drawn in two iterations.
  iterations: 2,
  // When drawing a bracket, this configures which side(s) of the element to bracket.
  brackets: `right`
}
import VueRoughNotation from 'vue-rough-notation'
Vue.use(VueRoughNotation, options)

import imagesloaded from 'imagesloaded'
Vue.prototype.$imagesLoaded = imagesloaded || new Vue()

import VueFullPage from 'vue-fullpage.js'
Vue.use(VueFullPage)
Vue.prototype.$eventHub = Vue.prototype.$eventHub || new Vue() // ÂπøÊí≠

import animated from 'animate.css' // npm install animate.css --saveÂÆâË£ÖÔºåÂú®ÂºïÂÖ•
Vue.use(animated)

import AOS from 'aos'
import 'aos/dist/aos.css'
AOS.init()

// global import gsap, Why can't add `{}` ?
import GSAP from 'gsap'
Vue.prototype.$GSAP = GSAP || new Vue()

function logSth() {
  const styles = [
    'color: green',
    'font-size: 20px',
    'font-family: ÂçéÊñá‰ªøÂÆã',
    'text-shadow: 2px 2px black',
    'padding: 10px'
  ].join(';')
  // ‰º†ÂÖ•Ê†∑Âºè
  console.log('%cHello There, This is Vanilla', styles)

  // Â±èËîΩÂêéÈù¢ÁöÑconsole
  const logDebug = true
  console.log = (function(oriLogFunc) {
    return function() {
      if (logDebug) {
        oriLogFunc.apply(this, arguments)
      }
    }
  })(console.log)
}
logSth()

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

// Ë∑ØÁî±ÂÖ®Â±ÄÂÆàÂç´
// ÂΩìË∑ØÁî±ËøõÂÖ•Ââç
// ÊúÄÂ•ΩÊîæÂú®new Vue‰∏ãÈù¢ÔºåÂà∑Êñ∞ÂíåÁ¨¨‰∏ÄÊ¨°ËøõÂÖ•‰∏ç‰ºöËß¶ÂèëbeforeEach,
const routerLogStyles = [
  'color: red',
  'padding: 10px'
].join(';')
router.beforeEach((to, from, next) => {
  console.log('%cbeforeEach--' + 'to.name:' + to.name + ',' + 'from.name:' + from.name, routerLogStyles)
  if (to.matched.length === 0) { // Â¶ÇÊûúÊú™ÂåπÈÖçÂà∞Ë∑ØÁî±
    console.log('stop')
    from.name ? next({
      name: from.name
    }) : next('/') // Â¶ÇÊûú‰∏äÁ∫ß‰πüÊú™ÂåπÈÖçÂà∞Ë∑ØÁî±ÂàôË∑≥ËΩ¨ÁôªÂΩïÈ°µÈù¢ÔºåÂ¶ÇÊûú‰∏äÁ∫ßËÉΩÂåπÈÖçÂà∞ÂàôËΩ¨‰∏äÁ∫ßË∑ØÁî±
  } else {
    store.commit('toggleOverlay')
    next() // Â¶ÇÊûúÂåπÈÖçÂà∞Ê≠£Á°ÆË∑≥ËΩ¨
  }

  const overlays = document.querySelector('.shape-overlays')
  if (from.name === 'Home') {
    overlays.classList.remove('shape-overlays-others-to-home')
    overlays.classList.add('shape-overlays-home-to-others')
  } else {
    overlays.classList.remove('shape-overlays-home-to-others')
    overlays.classList.add('shape-overlays-others-to-home')
  }
})

router.afterEach((to, from) => {
  if (from.name !== null) {
    store.commit('toggleOverlay')
    if (to.name === 'Home') {
      console.log('store afterEach initHomeAnim')
      store.commit('initHomeAnim')
    }
    if (to.name === 'About') {
      console.log('store afterEach initAboutAnim')
      store.commit('initAboutAnim')
    }
    if (to.name === 'Blog') {
      console.log('store afterEach initBlogAnim')
      store.commit('initBlogAnim')
    }
  }
  console.log('%cafterEach--' + 'to.name:' + to.name + ',' + 'from.name:' + from.name, routerLogStyles)
})
